html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  background: #ffc600;
  font-size: 10px;
}

.photobooth {
  max-width: 150rem;
  background: #fff;
  border-radius: 2px;
  margin: 2rem auto;
}

.photobooth:after {
  content: "";
  clear: both;
  display: block;
}

.photo {
  width: 100%;
  float: left;
}

.player {
  width: 200px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.strip {
  padding: 2rem;
}

.strip img {
  width: 100px;
  background: #fff;
  padding: .8rem .8rem 2.5rem;
  overflow-x: scroll;
  box-shadow: 0 0 3px rgba(0, 0, 0, .2);
}

.strip a:nth-child(5n+1) img {
  transform: rotate(10deg);
}

.strip a:nth-child(5n+2) img {
  transform: rotate(-2deg);
}

.strip a:nth-child(5n+3) img {
  transform: rotate(8deg);
}

.strip a:nth-child(5n+4) img {
  transform: rotate(-11deg);
}

.strip a:nth-child(5n+5) img {
  transform: rotate(12deg);
}

